import React, { Fragment } from 'react'
import './App.scss'

import { Routes } from './components/routes'
import { Footer } from './components/footer'
import { Header } from './components/header'

const App = () =>
  (
    <div className="App">
      <Fragment>
        <Header />
        <Routes />
        <Footer bgTemplate="template1" />
      </Fragment>
    </div>)

export default App



